export class Transform {
    get scaleX() {
        return this._scaleX;
    }
    get scaleY() {
        return this._scaleY;
    }
    get skewX() {
        return this._skewX;
    }
    get skewY() {
        return this._skewY;
    }
    get translateX() {
        return this._translateX;
    }
    get translateY() {
        return this._translateY;
    }
    constructor() {
        this.identity();
    }
    identity() {
        this._scaleX = 1;
        this._scaleY = 1;
        this._skewX = 0;
        this._skewY = 0;
        this._translateX = 0;
        this._translateY = 0;
        return this;
    }
    transform(options) {
        const scaleX = this._scaleX;
        const scaleY = this._scaleY;
        const skewX = this._skewX;
        const skewY = this._skewY;
        const translateX = this._translateX;
        const translateY = this._translateY;
        this._scaleX = scaleX * options.scaleX + skewX * options.skewY;
        this._scaleY = scaleY * options.scaleY + skewY * options.skewX;
        this._skewX = scaleX * options.skewX + skewX * options.scaleY;
        this._skewY = skewY * options.scaleX + scaleY * options.skewY;
        this._translateX = scaleX * options.translateX + skewX * options.translateY + translateX;
        this._translateY = scaleY * options.translateY + skewY * options.translateX + translateY;
    }
    rotate(radians) {
        const cos = Math.cos(radians);
        const sin = Math.sin(radians);
        this.transform({
            scaleX: cos,
            skewX: -sin,
            translateX: 0,
            scaleY: cos,
            skewY: sin,
            translateY: 0
        });
        return this;
    }
    scale(x, y) {
        this.transform({
            scaleX: x,
            skewX: 0,
            translateX: 0,
            scaleY: y,
            skewY: 0,
            translateY: 0
        });
        return this;
    }
    skew(x, y) {
        this.transform({
            scaleX: 1,
            skewX: x,
            translateX: 0,
            scaleY: 1,
            skewY: y,
            translateY: 0
        });
        return this;
    }
    translate(x, y) {
        this.transform({
            scaleX: 1,
            skewX: 0,
            translateX: x,
            scaleY: 1,
            skewY: 0,
            translateY: y
        });
        return this;
    }
    transformPoint(point) {
        return {
            x: point.x * this._scaleX + point.y * this._skewX + this._translateX,
            y: point.y * this._scaleY + point.x * this._skewY + this._translateY
        };
    }
}
